body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#2b2b2b}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.videos-container{display:flex;flex-wrap:wrap;justify-content:space-around;background-color:orange}.videos-container>h2{width:100%;text-align:center;font-size:2em;margin-bottom:0}.video-container{height:280px;width:320px;background-color:#2b2b2b}main{width:80%;height:70vh;min-height:600px;background-color:#e6a550;margin:auto;box-shadow:-5px 5px black}h1{font-family:StaatLiches;font-size:100px;text-align:center}video{width:320px;height:280px}

.video-container{border:1px solid black;margin:50px}

.landing-button{font-size:52px;font-family:StaatLiches;padding:20px;background-color:orange;border:3px solid red;width:400px}button:focus{outline:none}.container{display:flex;justify-content:space-around}.input-slider{position:relative;transition:height 0.5s;overflow:hidden;margin-top:10px;display:flex;flex-direction:column}.player-number>label{font-family:StaatLiches;font-size:32px}.game-name{display:flex;align-items:center;justify-content:center}.game-name>button{font-size:32px;font-family:StaatLiches;padding:0px 10px;background-color:orange;border:3px solid red}.select-selected{background-color:DodgerBlue}.select-selected:after{position:absolute;content:"";top:14px;right:10px;width:0;height:0;border:6px solid transparent;border-color:#fff transparent transparent transparent}.select-selected.select-arrow-active:after{border-color:transparent transparent #fff transparent;top:7px}.game-name>input{border:none;height:100%;background-color:#ae690f;flex-grow:1;font-family:StaatLiches;font-size:1.5em;padding:0 10px}

